@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.row{
  display: flex;
  flex-direction: row;
}

.row.reverse{
  flex-direction: row-reverse;
}

.column{
  display: flex;
  flex-direction: column;
}

.row .fillParent, .column .fillParent{
  flex: 1 1;
}

.row.centerH, .column.centerV{
  justify-content: center;
}

.row.centerV, .column.centerH{
  align-items: center;
}

.textCenter{
  text-align: center;
}

.positionRelative{
  position: relative;
}

.positionAbsolute{
  position: absolute;
}
.App{
  font-family: 'Fira Sans', sans-serif;

  --theme-white-color: #EFF2F5;
  --theme-dark-grey-color: #AEB1C1;
  --theme-light-grey-color: #B8CDD0;
  --theme-darkest-grey-color: #6F859A;
  --theme-black-color: #1C2E41;
  --theme-primary-color: #3c6aff;
  --theme-brown-color: #916A4A;

  height: 100vh;
  background-color: var(--theme-white-color);
  font-size: 1.85vh;

}

.btn{
  font-size: 1.25vh;
  color: white;
  background-color: var(--theme-primary-color);
  padding: 1vh 1.25vw;
  text-align: center;
  border-radius: 2vh;
  font-weight: 400;
  text-shadow: 0px 0px 1px white;
  box-shadow: 0px 0px 8px var(--theme-dark-grey-color);
}

.btn .icon{
  font-size: 1.2vh;
  margin-top: 0.3vh;
  margin-left: 0.1vw;
}
.Sidenav{
    width: 14vw;
    height: 93vh;
    padding-top: 7vh;
    background: white;
}

.Sidenav .action{
    padding: 1.5vh 1.5vw;
    border-bottom: 1px solid var(--theme-white-color);
    color: var(--theme-dark-grey-color);
}

.Sidenav .action i{
    transform: scale(0);
    -webkit-animation: grow 0.25s linear;
            animation: grow 0.25s linear;
    -webkit-animation-delay: 0.20s;
            animation-delay: 0.20s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.Sidenav .action .actionName{
    margin-left: 0.5vw;
    font-weight: bold;
    font-size: 1.5vh;
    -webkit-animation: fade-in 0.45s ease-in-out;
            animation: fade-in 0.45s ease-in-out;
}

@-webkit-keyframes fade-in{
    from {opacity: 0;}
    to   {opacity: 1;}
}

@keyframes fade-in{
    from {opacity: 0;}
    to   {opacity: 1;}
}

.Sidenav .action:nth-child(2){
    color: #000;
    color: initial;
    position: relative;
}

.Sidenav .action:nth-child(2) i{
    color: var(--theme-primary-color);
}

.Sidenav .action:nth-child(2)::after{
    content: '';
    position: absolute;
    right: 0vw;
    width: 0.2vw;
    height: 5vh;
    background-color: var(--theme-primary-color);
}

.Sidenav .action:nth-child(4){
    position: relative;
}

.Sidenav .action:nth-child(4)::after{
    content: '18';
    position: absolute;
    left: 6vw;
    font-size: 1.25vh;
    color: white;
    background-color: var(--theme-primary-color);
    padding: 0vh 0.35vw;
    border-radius: 0.75vh;
    transform: scale(0);
    -webkit-animation: grow 0.25s linear;
            animation: grow 0.25s linear;
    -webkit-animation-delay: 0.20s;
            animation-delay: 0.20s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.Sidenav .customerCareBtn{
    color: white;
    background-color: var(--theme-primary-color);
    width: 2.35vw;
    height: 4.5vh;
    margin: 2vh 1.5vw;
    border-radius: 50%;
    font-size: 2.5vh;
    -webkit-animation: fade-in 0.35s linear;
            animation: fade-in 0.35s linear;
}
.Navbar{
    background-color: white;
    height: 7vh;
    width: 98vw;
    padding: 0vh 1vw;
    box-shadow: 0px -8px 24px var(--theme-dark-grey-color);
}

.Navbar .brandName{
    color: var(--theme-black-color);
    font-weight: 700;
    padding-left: 0.5vw;
    padding-right: 0.25vw;
    text-transform: uppercase;
}

.Navbar .brandIcon{
    margin-right: 1vw;
}

.Navbar .brandIcon::before, .Navbar .brandIcon::after{
    content: '';
    position: absolute;
    height: 6px;
    width: 6px;
}

.Navbar .brandIcon::before{
    background-color: var(--theme-primary-color);
    top: -6px;
}

.Navbar .brandIcon::after{
    background-color: var(--theme-black-color);
    left: 6px;
}

.Navbar .tabs{
    margin-left: 8.5vw;
}

.Navbar .tabs .tab{
    font-weight: bold;
    font-size: 1.45vh;
    padding: 0vh 2vw;
    color: var(--theme-dark-grey-color);
}

.Navbar .tabs .tab:nth-child(1){
    color: var(--theme-primary-color);
}

.Navbar .tabs .tab:nth-child(1)::before{
    content: '';
    position: absolute;
    background-color: var(--theme-primary-color);
    width: 1.75vw;
    height: 0.35vh;
    bottom: -2.8vh;
    transform-origin: left;
    -webkit-animation: growAlongX 0.2s linear;
            animation: growAlongX 0.2s linear;
}

@-webkit-keyframes growAlongX{
    from {transform: scaleX(0);}
    to   {transform: scaleX(1);}
}

@keyframes growAlongX{
    from {transform: scaleX(0);}
    to   {transform: scaleX(1);}
}

.Navbar .globalSearch{
    color: var(--theme-dark-grey-color);
    background-color: var(--theme-white-color);
    padding: 0.5vh 1vw; 
    border-radius: 2vh;
    font-weight: bold;
}

.Navbar .globalSearch input{
    border: none;
    outline: none;
    width: 20vw;
    font-size: 1.35vh;
    font-family: inherit;
    font-weight: bold;
    background-color: inherit;
}

.Navbar .globalSearch .icon{
    padding-top: 0.45vh;
}

.Navbar .userAvatar{
    width: 1.75vw;
    margin: 0vh 2vw;
    -webkit-animation: grow 0.25s ease-in-out;
            animation: grow 0.25s ease-in-out;
}

.Navbar .userAvatar img{
    width: 100%;
}

.Navbar .notificationIcon{
    margin-left: 2vw;
    margin-right: 4vw;
    padding-top: 0.5vh;
    color: var(--theme-dark-grey-color);
    font-size: 2.5vh;
}

.Navbar .notificationIcon::before{
    content: '';
    position: absolute;
    background-color: var(--theme-primary-color);
    height: 6px;
    width: 6px;
    border-radius: 50%;
    right: -0.35vw;
    -webkit-animation: grow 0.35s linear;
            animation: grow 0.35s linear;
}

@-webkit-keyframes grow{
    from {transform: scale(0);}
    to   {transform: scale(1);}

}

@keyframes grow{
    from {transform: scale(0);}
    to   {transform: scale(1);}

}

.Navbar .btn{
    transform: scale(0);
    -webkit-animation: grow 0.25s ease-in-out;
            animation: grow 0.25s ease-in-out;
    -webkit-animation-delay: 0.35s;
            animation-delay: 0.35s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
.HomePage{
    padding-top: 14vh;
    padding-left: 22vw;
    padding-right: 8vw;
}

.HomePage .viewSelector{
    background: white;
    color: var(--theme-dark-grey-color);
    border-radius: 3vh;
    padding: 2vh 1.5vw;
    margin: 0vh 4vw;
}

.HomePage .viewSelector .view{
    font-weight: bold;
    font-size: 1.5vh;
    margin-right: 2vw;
}

.HomePage .viewSelector .view:nth-child(1){
    position: relative;
    color: var(--theme-primary-color);
}

.HomePage .viewSelector .view:nth-child(1)::after{
    content: '';
    position: absolute;
    background-color: var(--theme-primary-color);
    width: 1.75vw;
    height: 0.35vh;
    bottom: -2.2vh;
    left: 0.25vh;
    transform-origin: left;
    -webkit-animation: growAlongX 0.2s linear;
            animation: growAlongX 0.2s linear;
}

.HomePage .viewSelector i{
    transform: scale(0);
    -webkit-animation: grow 0.35s linear;
            animation: grow 0.35s linear;
    -webkit-animation-delay: 0.25s;
            animation-delay: 0.25s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.HomePage .viewSelector i:nth-child(3){
    margin-right: 2vw;
}

.HomePage .viewSelector i:nth-child(4){
    color: var(--theme-primary-color);
}

.HomePage .content{
    margin-left: 3.5vw;
}











.HomePage .Card{
    width: 12.5vw;
    height: 25vh;
    padding: 2vh 1vw;
    margin: 2.75vh 0.65vw;
    border-radius: 2.5vh;
    box-shadow: 0px 11px 35px -14px var(--theme-darkest-grey-color);
    -webkit-animation: fade-in 0.35s ease-in-out;
            animation: fade-in 0.35s ease-in-out;
}

.HomePage .Card .header .iconBtn{
    color: var(--theme-dark-grey-color);
    background: white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 3vh;
    width: 1.6vw;
}

.HomePage .Card .header .iconBtn:nth-child(1){
    margin-left: 0.5vw;
}

.HomePage .PictureCard{
    background-size: cover;
    background-repeat: no-repeat;
}

.HomePage .PictureCard::before{
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0vh;
    left: 0vw;
    border-radius: inherit;
    background-color: black;
    opacity: 0.3;
}

.HomePage .PictureCard .content{
    position: relative;
    margin-left: 0vw;
    color: white;
}

.HomePage .PictureCard .title{
    margin-top: 14vh;
    font-weight: 500;
    font-size: 1.75vh;
    text-shadow: 0px 0px 2px var(--theme-darkest-grey-color);
}

.HomePage .PictureCard .tags{
    font-size: 1.35vh;
    margin: 1vh 0vw;
    opacity: 0.7;
}

.HomePage .PictureCard .subtitle{
    font-size: 1.5vh;
    font-weight: bold;
}

















.HomePage .NewsCard{
    background: white;
}

.HomePage .NewsCard::before{
    content: '';
    position: absolute;
}

.HomePage .NewsCard .bg{
    height: 15.5vh;
    width: 100%;
    background-size: cover;
    top: 0vh;
    left: 0vw;
    border-top-left-radius: 2.5vh;
    border-top-right-radius: 2.5vh;
}

.HomePage .NewsCard .heading{
    margin-top: 14vh;
    font-weight: 500;
    font-size: 1.75vh;
    text-shadow: 0px 0px 2px var(--theme-light-grey-color);
}

.HomePage .NewsCard .description{
    font-size: 1.25vh;
    text-align: justify;
    opacity: 0.5;
    margin-top: 1vh;
}
